import '../styles/index.scss';

import $ from 'jquery';
import 'bootstrap';
import SmoothScroll from 'smooth-scroll';
import LazyLoad from "vanilla-lazyload";

let lazyload;

function smoothScroll() {
    const scroll = new SmoothScroll();

    $(document).ready(() => {
        let mov = false;
        $("a").on('click', function (e) {
            if (this.hash == "" || this.hash == '#!' || $(this).attr('href')[0] != '#' || mov)
                return;

            scroll.cancelScroll();
            mov = true;

            e.preventDefault();

            let hash = this.hash;
            
            function doWork(){
                if(!window.matchMedia('(max-width: 991.98px)').matches)
                    scroll.animateScroll($(hash).offset().top);
                else //versao mobile
                    scroll.animateScroll($(hash).offset().top - ($(hash).data('scroll-center-mobile') ? 0 : $('#navbar').height()));

                setTimeout(() => mov = false, 500);
            }

            if(!$(this).data('toggle') == 'collapse')
                requestAnimationFrame(() => doWork());
            else
                setTimeout(() => doWork(), 400);
        });
    });
}

$(document).ready(() => {
    if(navigator.platform.toUpperCase().indexOf('WIN') !== -1)
        $(document.body).addClass('windows');

    $('#btn-show-form1').click(() => {
        $('#page7').removeClass('d-none').addClass('d-flex');
    });

    function lazyStart(mobile){
        requestAnimationFrame(() => lazyload = new LazyLoad({
            callback_loaded: (el) => {
                if(!mobile && $(el).hasClass('img-parallax'))
                    requestAnimationFrame(() => parallax(el));
            }
        }));
    }    

    setTimeout(() => $('.img-parallax').get().forEach(e => lazyload.load(e)), 1000);

    smoothScroll();

    if(window.matchMedia('(min-width: 992px)').matches){
        $('.page3 .box').click(function () {
            const el = $('#' + $(this).data('target'));
            $('.page3 .screen.active').removeClass('active');
            $('.page3 .box.active').removeClass('active');
            el.addClass('active');
            $(this).addClass('active');
        });

        lazyStart(false);
    }
    else {
        $('.page3 .box').removeClass('active');
        lazyStart(true);
    }

    //navbar mobile
    let navState = 0;
    if(window.matchMedia('(max-width: 991.98px)').matches){
        inlineMinHeight();
        setTimeout(() => inlineMinHeight, 300);
        window.addEventListener('orientationchange', () => setTimeout(() => {
            inlineMinHeight();
            requestAnimationFrame(() => {
                if(navState == 0)
                    $('.navbar-collapse').css('display', 'none');
            });
        }, 150));

        $('.navbar-collapse').css('display', 'none');

        let ypos = 0;
        $('.navbar-close, .nav-link').click(() => {
            if(navState != 2)
                return;
            navState = 1;

            $('body').css('overflow-y', '').css('-webkit-overflow-scrolling', '');;
            $('.main-content').css('display', '');
            $('.cta-mobile').attr('style', '');
            
            requestAnimationFrame(() => {
                window.scrollTo(0, ypos);
                requestAnimationFrame(() => scrollspy(true));
            });

            setTimeout(() => {
                $('.navbar-collapse').removeClass('show');
                $('.navbar-toggler').css('display', '');
                $('.navbar-close').css('display', '');

                navState = 0;

                setTimeout(() =>  $('.navbar-collapse').css('display', 'none'), 220);
            }, 100);
        });
        $('.navbar-toggler').click(() => {
            if(navState != 0)
                return;
            navState = 1;

            $('.navbar-collapse').css('display', '');
            scrollspy(false);

            setTimeout(() => {
                $('.navbar-collapse').addClass('show');
                $('.navbar-toggler').css('display', 'none');
                $('.navbar-close').css('display', 'unset');

                ypos = window.scrollY;

                setTimeout(() => {
                    $('body').css('overflow-y', 'hidden').css('-webkit-overflow-scrolling', 'touch');
                    $('.main-content').css('display', 'none');
                    $('.cta-mobile').attr('style', 'display: none!important');

                    navState = 2;
                }, 190);
            }, 10);
        });

        scrollspy(true);
    }

    setupFormularioEmpresa();
    setupFormularioDuvida();
});

function scrollspy(enable){
    if(enable || enable == null) 
        $('body').scrollspy({ target: '#navbar', offset: $('#navbar').height() + 5 });
    else
        $('body').scrollspy('dispose');
}

function inlineMinHeight(){
    $('.m-h-100').css('min-height', window.innerHeight + 'px');
    $('.navbar-collapse').attr('style', 'height: ' + (window.innerHeight - 62) + 'px');
}

function parallax(el){
    /*
        Parallax
        From: https://codepen.io/RenanB/pen/GZeBNg
    */
    $(el == null ? '.img-parallax' : el).each(function () {
        let img = $(this);
        let imgParent = $(this).parent();
        function parallaxImg() {
            let speed = img.data('speed');
            let imgY = imgParent.offset().top;
            let winY = $(window).scrollTop();
            let winH = $(window).height();
            let parentH = imgParent.innerHeight();


            // The next pixel to show on screen      
            let winBottom = winY + winH;
            let imgPercent;

            // If block is shown on screen
            if (winBottom > imgY && winY < imgY + parentH) {
                // Number of pixels shown after block appear
                let imgBottom = ((winBottom - imgY) * speed);

                // Max number of pixels until block disappear
                let imgTop = winH + parentH;

                // Porcentage between start showing until disappearing
                imgPercent = ((imgBottom / imgTop) * 100) + (50 - (speed * 50));
            }

            img.css({
                top: imgPercent + '%',
                transform: 'translate(-50%, -' + imgPercent + '%)'
            });
        }
        $(document).on({
            scroll: function () {
                parallaxImg();
            }, ready: function () {
                parallaxImg();
            }
        });
    });
}

window.$ = $;

function setupFormularioDuvida(){
    $('.page8 .enviar').click(() => {
        const data = {
            nome: $('.page8 .nome').val(),
            telefone: $('.page8 .telefone').val(),
            email: $('.page8 .email').val(),
            mensagem: $('.page8 .mensagem').val(),
        };

        $.ajax('/api/duvida', {
            type : 'POST',
            contentType : 'application/json',
            data : JSON.stringify(data),
            success(){
                $('.page8 .alert').removeClass('d-none').text('Sua mensagem foi enviada com sucesso');
            },
            error(ex){                
                if(!ex.responseJSON || ex.responseJSON.statusCode == 500)
                    $('.page8 .alert').removeClass('d-none').text('Erro interno do servidor');
                else
                    $('.page8 .alert').removeClass('d-none').text('Preencha todos os campos corretamente e tente novamente');
                    // $('.page8 .alert').removeClass('.d-none').text(ex.responseJSON.message);
            }
        });
    });
}

function setupFormularioEmpresa(){
    $('.page7 .enviar').click(() => {
        const data = {
            categoria: $('.page7 .categoria').val(),
            nome: $('.page7 .nome').val(),
            empresa: $('.page7 .empresa').val(),
            telefone: $('.page7 .telefone').val(),
            email: $('.page7 .email').val(),
            mensagem: $('.page7 .mensagem').val(),
        };

        $.ajax('/api/empresa', {
            type : 'POST',
            contentType : 'application/json',
            data : JSON.stringify(data),
            success(){
                $('.page7 .alert').removeClass('d-none').removeClass('alert-danger').addClass('alert-success').text('Sua mensagem foi enviada com sucesso');
            },
            error(ex){                
                if(!ex.responseJSON || ex.responseJSON.statusCode == 500)
                    $('.page7 .alert').removeClass('d-none').removeClass('alert-success').addClass('alert-danger').text('Erro interno do servidor');
                else
                    $('.page7 .alert').removeClass('d-none').removeClass('alert-success').addClass('alert-danger').text('Preencha todos os campos corretamente e tente novamente');
                    // $('.page8 .alert').removeClass('.d-none').text(ex.responseJSON.message);
            }
        });
    });
}